* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
}

.wrapper p {
  font-size: 40px;
  font-weight: bold;
  margin: 0 0 20px 0;
}

.wrapper button {
  background-color: #000;
  color: #fff;
  text-transform: capitalize;
  padding: 5px 20px;
  font-size: 20px;
  border: none;
}